import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LeadModule } from './lead/lead.module';
import { DealsModule } from './deals/deals.module';
import { QuotesModule } from './quotes/quotes.module';
import { CustomerComponent } from './customer.component';
import { NewCustomerComponent } from './new-customer/new-customer.component';
import { CustBreadcrumbComponent } from './common/cust-breadcrumb/cust-breadcrumb.component';
import { ViewCustomerComponent } from './view-customer/view-customer.component';

@NgModule({
  imports: [
    CommonModule,
    LeadModule,
    DealsModule,
    QuotesModule
  ],
  declarations: [CustomerComponent, NewCustomerComponent, CustBreadcrumbComponent, ViewCustomerComponent],
  exports:[CustBreadcrumbComponent]
})
export class CustomerModule { }



