import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-project',
  templateUrl: './new-project.component.html',
  styleUrls: ['./new-project.component.css']
})
export class NewProjectComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    });
    $(".custprior,.productgroup,.milestone").select2();
    $('.summernote').summernote({
      toolbar: [
        // [groupName, [list of button]]
        ['style', ['bold', 'italic', 'underline', 'clear']],
        ['font', ['strikethrough', 'superscript', 'subscript']],
        ['fontsize', ['fontsize']],
        ['color', ['color']],
        ['para', ['ul', 'ol', 'paragraph']],
        ['height', ['height']]
      ],
      height: 100
    });
    var milestone = $('.pro-milestone').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "width": "120px",
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }, {
        'targets': 0,
        'searchable': false,
        'orderable': false,
        'className': 'dt-body-center',
        'render': function (data, type, full, meta) {
          return '<input type="checkbox" class="i-checks" name="id[]" value="'
            + $('<div/>').text(data).html() + '">';
        }
      }]
    });
    $(".addmilestone").click(function () {
      milestone.row.add(['', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control  input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id="">', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id=""></div>', '']).draw(false);
    });
    var task = $('.pro-task').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "width": "120px",
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }, {
        'targets': 0,
        'searchable': false,
        'orderable': false,
        'className': 'dt-body-center',
        'render': function (data, type, full, meta) {
          return '<input type="checkbox" class="i-checks" name="id[]" value="'
            + $('<div/>').text(data).html() + '">';
        }
      }]
    });
    $(".addtask").click(function () {
      task.row.add(['', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control  input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id="">', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id=""></div>', '']).draw(false);
    });
    var grnpart = $('.grn-part').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addpartRow").click(function () {
      grnpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Pallet">Pallet</option></select></div>', '']).draw(false);
    });
    var grnsubpart = $('.grn-subpart').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addsubpartRow").click(function () {
      grnsubpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Pallet">Pallet</option></select></div>', '']).draw(false);
    });
    var taskinventory = $('.task-inventory').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-primary btn-xs' data-toggle='modal' data-target='#addinventory'>Add Inventory</button>"
      }]
    });

    $('.example-select-all').on('click', function () {
      // Check/uncheck all checkboxes in the table
      var rows = milestone.rows({ 'search': 'applied' }).nodes();
      $('input[type="checkbox"]', rows).prop('checked', this.checked);
    });

    // Handle click on checkbox to set state of "Select all" control
    $('.pro-milestone tbody').on('change', 'input[type="checkbox"]', function () {
      // If checkbox is not checked
      if (!this.checked) {
        var el = $('.example-select-all').get(0);
        // If "Select all" control is checked and has 'indeterminate' property
        if (el && el.checked && ('indeterminate' in el)) {
          // Set visual state of "Select all" control 
          // as 'indeterminate'
          el.indeterminate = true;
        }
      }
    });
  }

}



