import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../app.helpers'
declare var $: any;
declare var jQuery: any;


@Component({
  selector: 'app-resources',
  templateUrl: './resources.component.html',
  styleUrls: ['./resources.component.css']
})
export class ResourcesComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $(".productgroup,.projectno").select2();
    var table = $('.table-resources').DataTable({
      'columnDefs': [{
                "targets": -1,
                "data": null,
                'searchable': false,
                'orderable': false,
                "defaultContent": "<a class='btn btn-primary btn-xs'><i class='fa fa-eye'></i></a> <a class='btn btn-primary btn-xs'><i class='fa fa-download'></i></a> <a class='btn btn-primary btn-xs'><i class='fa fa-trash'></i></a>"
            }],
      pageLength: 10,
      responsive: true,
      
    });
  }

}



