import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../../app.helpers';
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-deal',
  templateUrl: './new-deal.component.html',
  styleUrls: ['./new-deal.component.css']
})
export class NewDealComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".dealowner, .leadsource, .Companyname, .dealstage, .dealtype, .contact").select2();
     $('.input-group.date').datepicker({
                todayBtn: "linked",
                keyboardNavigation: false,
                forceParse: false,
                calendarWeeks: true,
                autoclose: true
            });
    
    
  }

}



