import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TicketsModule } from './tickets/tickets.module';
import { OnsiteServiceTicketModule } from './onsite-service-ticket/onsite-service-ticket.module';
import { ServiceTicketRequestComponent } from './service-ticket-request/service-ticket-request.component';
import { KnowledgeBaseModule } from './knowledge-base/knowledge-base.module';
import { SupportComponent } from './support.component';
import { ForumsComponent } from './forums/forums.component';
import { ForumPostComponent } from './forums/forum-post/forum-post.component';
import { FeedbackModule } from './feedback/feedback.module';

@NgModule({
  imports: [
    CommonModule,
    TicketsModule,
    KnowledgeBaseModule,
    OnsiteServiceTicketModule,
    FeedbackModule
  ],
  declarations: [ServiceTicketRequestComponent,SupportComponent, ForumsComponent, ForumPostComponent]
})
export class SupportModule { }



