import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { smoothlyMenu, popupmodule } from '../../../app.helpers';
import { Subject } from 'rxjs/Rx';
declare var jQuery: any;
declare var $: any;
@Component({
  selector: 'topnavbar',
  templateUrl: 'topnavbar.template.html',
  styleUrls: ['./topnavbar.component.css']
})

export class TopnavbarComponent implements OnInit {
  links = [];
  sublinks = [];
  isShown = false;
  firstRightClick;
  submenuclick;
  constructor(private router: Router) { }
  activeRoute(routename: string): boolean {
    return this.router.url.indexOf(routename) > -1;
  }
  ngOnInit() {
    this.links = [
      { title: 'Dashboard', id: 'Dashboard', url: 'dashboard', icon: 'fa fa-dashboard' },
      { title: 'Relationships', id: 'Relationships', url: 'relationship', icon: 'fa fa-users' },
      { title: 'Contracts', id: 'Contracts', url: 'contract', icon: 'fa fa-handshake-o' },
      { title: 'Projects', id: 'Projects', url: 'project', icon: 'fa fa-suitcase' },
      { title: 'Support', id: 'Support', url: 'support', icon: 'fa fa-headphones' },
      { title: 'Inventory', id: 'Inventory', url: 'inventory', icon: 'fa fa-cubes' },
      { title: 'Service', id: 'Service', url: 'service', icon: 'fa fa-wrench' },
      { title: 'Intelligence', id: 'Intelligence', url: 'intelligence', icon: 'fa fa-pie-chart' },
      { title: 'Settings', id: 'Settings', url: 'settings', icon: 'fa fa-cogs' }
    ];
    this.firstRightClick = { title: 'Dashboard', id: 'Dashboard', url: 'dashboard', icon: 'fa fa-dashboard' };
    //this.links.forEach(l => l.subject.subscribe(val=> this.firstCallback(val)));

    popupmodule();

    var hidWidth;
    var scrollBarWidths = 40;

    var widthOfList = function () {
      var itemsWidth = 0;
      $('.list li').each(function () {
        var itemWidth = $(this).outerWidth();
        itemsWidth += itemWidth;
      });
      return itemsWidth;
    };

    var widthOfHidden = function () {
      return (($('.modulewrapper').outerWidth()) - widthOfList() - getLeftPosi()) - scrollBarWidths;
    };

    var getLeftPosi = function () {
      return $('.list').position().left;
    };

    var reAdjust = function () {
      if (($('.modulewrapper').outerWidth()) < widthOfList()) {
        $('.scroller-right').show();
      }
      else {
        $('.scroller-right').hide();
      }

      if (getLeftPosi() < 0) {
        $('.scroller-left').show();
      }
      else {
        $('.item').animate({ left: "-=" + getLeftPosi() + "px" }, 'slow');
        $('.scroller-left').hide();
      }
    }

    reAdjust();

    $(window).on('resize', function (e) {
      reAdjust();
    });

    $('.scroller-right').click(function () {

      $('.scroller-left').fadeIn('slow');
      $('.scroller-right').fadeOut('slow');

      $('.list').animate({ left: "+=" + widthOfHidden() + "px" }, 'slow', function () {

      });
    });

    $('.scroller-left').click(function () {

      $('.scroller-right').fadeIn('slow');
      $('.scroller-left').fadeOut('slow');

      $('.list').animate({ left: "-=" + getLeftPosi() + "px" }, 'slow', function () {

      });
    });

    $('#Relationshipsshow').hide();
    $('#Contractsshow').hide();
    $('#Projectsshow').hide();
    $('#Supportshow').hide();
    $('#Inventoryshow').hide();
    $('#Serviceshow').hide();
    $('#Intelligenceshow').hide();
    $('#Settingsshow').hide();
    $('#clicks').on('click', 'li', function (event) {
      var tabId = $(this).attr('id');
      $('#tabShows ul').hide();
      $('#' + tabId + 'show').show();
    });

  }
  toggleNavigation(): void {
    jQuery("body").toggleClass("mini-navbar");
    smoothlyMenu();
  }
  firstCallback(val) {
    debugger
    this.firstRightClick = val;

  }

}



