import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-kb',
  templateUrl: './new-kb.component.html',
  styleUrls: ['./new-kb.component.css']
})
export class NewKbComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $(document).on('mouseenter', '.chat-message', function () {
      $(this).find(".btn-msg-remove").show();
    }).on('mouseleave', '.chat-message', function () {
      $(this).find(".btn-msg-remove").hide();
    });
    $('.summernote').summernote({
  toolbar: [
    // [groupName, [list of button]]
    ['style', ['bold', 'italic', 'underline', 'clear']],
    ['font', ['strikethrough', 'superscript', 'subscript']],
    ['fontsize', ['fontsize']],
    ['color', ['color']],
    ['para', ['ul', 'ol', 'paragraph']],
    ['height', ['height']]
  ],
  height:100
    });
  }

}



