import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-service-ticket-request',
  templateUrl: './service-ticket-request.component.html',
  styleUrls: ['./service-ticket-request.component.css']
})
export class ServiceTicketRequestComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    $(".country, .status").select2();
  }

}



