import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-view-milestone',
  templateUrl: './view-milestone.component.html',
  styleUrls: ['./view-milestone.component.css']
})
export class ViewMilestoneComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    var task = $('.pro-task').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }, {
        'targets': 0,
        'searchable': false,
        'orderable': false,
        'className': 'dt-body-center',
        'render': function (data, type, full, meta) {
          return '<input type="checkbox" class="i-checks" name="id[]" value="'
            + $('<div/>').text(data).html() + '">';
        }
      }]
    });
    $(".addtask").click(function () {
      task.row.add(['', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control  input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id="">', '<div class="form-group col-lg-12 dyntbl-tbox"><input type="text" value="" class="form-control min="1" input-sm" id=""></div>', '']).draw(false);
    });
    var grnpart = $('.grn-part').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addpartRow").click(function () {
      grnpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Pallet">Pallet</option></select></div>', '']).draw(false);
    });
    $('.summernote').summernote({
      toolbar: [
        // [groupName, [list of button]]
        ['style', ['bold', 'italic', 'underline', 'clear']],
        ['font', ['strikethrough', 'superscript', 'subscript']],
        ['fontsize', ['fontsize']],
        ['color', ['color']],
        ['para', ['ul', 'ol', 'paragraph']],
        ['height', ['height']]
      ],
      height: 100
    });
    $(".productgroup,.status,.priority,.completed").select2();
    var table = $('.tasks-table').DataTable({
      pageLength: 10,
      responsive: true,
      dom: '<"html5buttons"B>lTfgitp',

      buttons: [
        { extend: 'copy' },
        { extend: 'csv' },
        { extend: 'excel', title: 'ExampleFile' },
        { extend: 'pdf', title: 'ExampleFile' },

        {
          extend: 'print',
          customize: function (win) {
            $(win.document.body).addClass('white-bg');
            $(win.document.body).css('font-size', '10px');

            $(win.document.body).find('table')
              .addClass('compact')
              .css('font-size', 'inherit');
          }
        }
      ],
      'order': [1, 'asc']

    });
    $("#sparkline6").sparkline([5, 3], {
      type: 'pie',
      height: '24',
      sliceColors: ['#1ab394', '#d7d7d7']
    });

    // Handle click on "Select all" control
    $('#example-select-all').on('click', function () {
      // Check/uncheck all checkboxes in the table
      var rows = table.rows({ 'search': 'applied' }).nodes();
      $('input[type="checkbox"]', rows).prop('checked', this.checked);
    });

    // Handle click on checkbox to set state of "Select all" control
    $('#example tbody').on('change', 'input[type="checkbox"]', function () {
      // If checkbox is not checked
      if (!this.checked) {
        var el = $('#example-select-all').get(0);
        // If "Select all" control is checked and has 'indeterminate' property
        if (el && el.checked && ('indeterminate' in el)) {
          // Set visual state of "Select all" control 
          // as 'indeterminate'
          el.indeterminate = true;
        }
      }
    });

    $('a.toggle-vis').on('click', function (e) {
      e.preventDefault();
      // Get the column API object
      var column = table.column($(this).attr('data-column'));
      // Toggle the visibility
      column.visible(!column.visible());
      $(this).toggleClass('itemselected');
    });
  }

}



