import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../app.helpers';
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $(".suppliercode, .suppliername, .suppliertype, .suppliercategory").select2();
    var table = $('.dataTables-example').DataTable({
      'columnDefs': [{

        'targets': 0,
        'searchable': false,
        'orderable': false,
        'className': 'dt-body-center',
        'render': function (data, type, full, meta) {
          return '<input type="checkbox" class="i-checks" name="id[]" value="'
            + $('<div/>').text(data).html() + '">';
        }
      }, { 'visible': false, 'targets': [6,7,8,9,10,13, 14, 15, 16,17] }, {
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<a href='#new-part' class='btn btn-primary btn-xs'><i class='fa fa-edit'></i></a>"
      }, {
        "targets": 1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<img alt='image'' src='assets/img/tower-ico.png' class='img-responsive img-thumbnail'>"
      }],
      pageLength: 10,
      responsive: true,
      dom: '<"html5buttons"B>lTfgitp',

      buttons: [
        { extend: 'copy' },
        { extend: 'csv' },
        { extend: 'excel', title: 'ExampleFile' },
        { extend: 'pdf', title: 'ExampleFile' },

        {
          extend: 'print',
          customize: function (win) {
            $(win.document.body).addClass('white-bg');
            $(win.document.body).css('font-size', '10px');

            $(win.document.body).find('table')
              .addClass('compact')
              .css('font-size', 'inherit');
          }
        }
      ],
      'order': [1, 'asc']

    });
     var table = $('.dataTables-subpart').DataTable({
      'columnDefs': [{

        'targets': 0,
        'searchable': false,
        'orderable': false,
        'className': 'dt-body-center',
        'render': function (data, type, full, meta) {
          return '<input type="checkbox" class="i-checks" name="id[]" value="'
            + $('<div/>').text(data).html() + '">';
        }
      }, { 'visible': false, 'targets': [6,7,8,9,10,13, 14, 15, 16,17] }, {
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<a href='#new-subpart' class='btn btn-primary btn-xs'><i class='fa fa-edit'></i></a>"
      }, {
        "targets": 1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<img alt='image'' src='assets/img/tower-ico.png' class='img-responsive img-thumbnail'>"
      }],
      pageLength: 10,
      responsive: true,
      dom: '<"html5buttons"B>lTfgitp',

      buttons: [
        { extend: 'copy' },
        { extend: 'csv' },
        { extend: 'excel', title: 'ExampleFile' },
        { extend: 'pdf', title: 'ExampleFile' },

        {
          extend: 'print',
          customize: function (win) {
            $(win.document.body).addClass('white-bg');
            $(win.document.body).css('font-size', '10px');

            $(win.document.body).find('table')
              .addClass('compact')
              .css('font-size', 'inherit');
          }
        }
      ],
      'order': [1, 'asc']

    });
    // Handle click on "Select all" control
    $('.example-select-all').on('click', function () {
      // Check/uncheck all checkboxes in the table
      var rows = table.rows({ 'search': 'applied' }).nodes();
      $('input[type="checkbox"]', rows).prop('checked', this.checked);
    });

    // Handle click on checkbox to set state of "Select all" control
    $('.example tbody').on('change', 'input[type="checkbox"]', function () {
      // If checkbox is not checked
      if (!this.checked) {
        var el = $('.example-select-all').get(0);
        // If "Select all" control is checked and has 'indeterminate' property
        if (el && el.checked && ('indeterminate' in el)) {
          // Set visual state of "Select all" control 
          // as 'indeterminate'
          el.indeterminate = true;
        }
      }
    });

    $('a.toggle-vis').on('click', function (e) {
      e.preventDefault();
      // Get the column API object
      var column = table.column($(this).attr('data-column'));
      // Toggle the visibility
      column.visible(!column.visible());
      $(this).toggleClass('itemselected');
    });
    
  }

}



