import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-adjustment',
  templateUrl: './new-adjustment.component.html',
  styleUrls: ['./new-adjustment.component.css']
})
export class NewAdjustmentComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    $(".quality, .uom,.type,.warehouse").select2();
    var grnpart = $('.grn-part').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addpartRow").click(function () {
      grnpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Pallet">Pallet</option></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '']).draw(false);
    });
    var grnsubpart = $('.grn-subpart').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addsubpartRow").click(function () {
      grnsubpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Pallet">Pallet</option></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '']).draw(false);
    });
  }

}



