import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ProTasksComponent } from './pro-tasks.component';
import { TaskDetailsComponent } from './task-details/task-details.component';
import { ViewProTaskComponent } from './view-pro-task/view-pro-task.component';
import {BrowserModule} from "@angular/platform-browser";
import {DndModule} from 'ng2-dnd';

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    DndModule.forRoot()
  ],
  declarations: [ProTasksComponent, TaskDetailsComponent, ViewProTaskComponent],
  exports: [BrowserModule, DndModule]
})
export class ProTasksModule { }



