import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-ticket-activity',
  templateUrl: './ticket-activity.component.html',
  styleUrls: ['./ticket-activity.component.css']
})
export class TicketActivityComponent implements OnInit {
  mailbox: boolean;
  constructor() { }

  ngOnInit() {
    this.mailbox = false;
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    $(".productgroup,.Asignee,.Priority,.department,.Manager, .ticketsstatus").select2();
    $('.summernote').summernote({
      toolbar: [
        // [groupName, [list of button]]
        ['style', ['bold', 'italic', 'underline', 'clear']],
        ['font', ['strikethrough', 'superscript', 'subscript']],
        ['fontsize', ['fontsize']],
        ['color', ['color']],
        ['para', ['ul', 'ol', 'paragraph']],
        ['height', ['height']]
      ],
      height: 200
    });
    $('.tagsinput').tagsinput({
      tagClass: 'label label-primary'
    });

    $(".dropdown-menu li a").click(function () {
      var selText = $(this).text();
      $(this).parents('.btn-group').find('.dropdown-toggle').html(selText + ' <span class="caret"></span>');
    });
    $('#sendmail').click(function () {
      $('.form-horizontal').removeClass('hide');
      $('.form-horizontal').toogleClass('show');
    });
  }
  togglemailbox() {
    this.mailbox = !this.mailbox;
    $('.tickets-chat')[0].focus();
  }

}



