import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from "@angular/router";
import { LocationStrategy, HashLocationStrategy } from '@angular/common';

import { ROUTES } from "./app.routes";
import { AppComponent } from './app.component';

import { AppService } from './app.service';

// App views
import { MainViewModule } from "./views/main-view/main-view.module";
import { MinorViewModule } from "./views/minor-view/minor-view.module";
import { LoginModule } from "./views/login/login.module";
import { RegisterModule } from "./views/register/register.module";

// App modules/components
import { LayoutsModule } from "./components/common/layouts/layouts.module";
import { DashboardModule } from './views/dashboard/dashboard.module';
import { ContractModule } from './views/contract/contract.module';
import { SupportModule } from './views/support/support.module';
import { InventoryModule } from './views/inventory/inventory.module';
import { ProjectModule } from './views/project/project.module';
import { ServiceModule } from './views/service/service.module';
import { RelationshipModule } from './views/relationship/relationship.module';
import { IntelligenceModule } from './views/intelligence/intelligence.module';
import { SettingsModule } from './views/settings/settings.module';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    // Angular modules
    BrowserModule,
    HttpModule,

    // Views
    MainViewModule,
    MinorViewModule,
    LoginModule,
    RegisterModule,
    DashboardModule,
    ContractModule,
    SupportModule,
    InventoryModule,
    ProjectModule,
    ServiceModule,
    RelationshipModule,
    SettingsModule,
    IntelligenceModule,
    // Modules
    LayoutsModule,
    RouterModule.forRoot(ROUTES)
  ],
  providers: [
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    },
    AppService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



