import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-subpart',
  templateUrl: './new-subpart.component.html',
  styleUrls: ['./new-subpart.component.css']
})
export class NewSubpartComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    $(".country, .uom, .condition, .suppliedby").select2();
  }

}



