import { Component, OnInit } from '@angular/core';
import { commonScripts } from '../../../../../app.helpers'
declare var $: any;
declare var jQuery: any;

@Component({
  selector: 'app-new-quality-change',
  templateUrl: './new-quality-change.component.html',
  styleUrls: ['./new-quality-change.component.css']
})
export class NewQualityChangeComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    commonScripts();
    $('.scroll_content').slimscroll({
      height: '400px'
    })
    $(".comapnyname").select2({
      placeholder: "Company Name",
      allowClear: true
    });
    $(".quality, .uom,.suppliercode").select2();
    var qualitypart = $('.quality-part').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".qualitypartRow").click(function () {
      qualitypart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Kilogram">Kilogram</option><option value="Litre">Litre</option><option value="Pounds">Pounds</option></select></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Good">Good</option><option value="Faulty">Faulty</option><option value="Damaged">Damaged</option></select></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Good">Good</option><option value="Faulty">Faulty</option><option value="Damaged">Damaged</option></select></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '']).draw(false);
    });
    var qualitysubpart = $('.quality-subpart').DataTable({
      pageLength: 10,
      //"sPaginationType": "full_numbers",
      //"bProcessing": true,
      "bDeferRender": true,
      'columnDefs': [{
        "targets": -1,
        "data": null,
        'searchable': false,
        'orderable': false,
        "defaultContent": "<button class='btn btn-danger btn-xs' data-toggle='tooltip' title='Remove'><i class='fa fa-remove'></i></button>"
      }]
    });
    $(".addqualitysubpartRow").click(function () {
      qualitysubpart.row.add(['<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id="" ></div>', '<div class="form-group dyntbl-tbox"><textarea class="form-control input-sm" ></textarea></div>', '<div class="form-group dyntbl-tbox"><input type="number" value="" class="form-control min="1" input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Box">Box</option><option value="Kilogram">Kilogram</option><option value="Litre">Litre</option><option value="Pounds">Pounds</option></select></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Good">Good</option><option value="Faulty">Faulty</option><option value="Damaged">Damaged</option></select></div>', '<div class="form-group dyntbl-tbox"><select class=" form-control input-sm" id=""><option value="Good">Good</option><option value="Faulty">Faulty</option><option value="Damaged">Damaged</option></select></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '<div class="form-group dyntbl-tbox"><input type="text" value="" class="form-control input-sm" id=""></div>', '']).draw(false);
    });
  }

}



