import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AppService } from '../../app.service';

@Component({
    selector: 'login',
    templateUrl: 'login.template.html',
    styleUrls: ['login.component.css']
})
export class loginComponent implements OnInit {
    lt: any;
    model: any = {};
    subDomainName: string;
    value:any;
    constructor(private _router: Router, private _appService: AppService ) { }

    ngOnInit() {
        this.value=1;
        this.subDomainName = this._appService.currentSubDomainName();
        this.model.username = "admin@nera.com";
        this.model.password = "hello";

       // this._appService.getAll().subscribe(res => this.lt = res);
        // console.log(this._appService.getAll(), this.lt);
        
    }

    login() {
        if ((this.model.username === "admin@nera.com") && (this.model.password === "hello")) {
            this._router.navigate(['dashboard']);
        } else {
            alert("username and password is invalid");
        }
    }
}



